body {
  margin: 0;
  padding: 0;
  background-color: rgb(181, 223, 195);
}
.questionContainer {
  display: flex;
  justify-content: center;
  margin-top: 24px;
}
.anserContainer {
  display: flex;
  justify-content: center;
  margin-top: 2px;
}
.question {
  text-align: center;
  display: inline-block;
  line-height: 150px;
  height: 150px;
  width: 150px;
  height: 150px;
  background-color: tomato;
  color: white;
  font-size: 70px;
}

.anser {
  text-align: center;
  display: inline-block;
  line-height: 150px;
  width: 150px;
  height: 150px;
  background-color: blue;
  color: white;
  font-size: 100px;
}

.start {
  color: blue;
}

.anserContainer {
  background-color: rgb(181, 223, 195);
  display: flex;
  justify-content: center;
  margin-top: 2px;
  height: 50px;
}

input {
  width: 224px;
  font-size: 30px;
}
button {
  width: 70px;
}
